import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import msgBox from '@/components/msgBox'
import cityList from '@/components/cityList/cityList'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path     : '/',
      name     : 'Index',
      component: Index,
      meta     : {
        title: '',
      }
    },
    {
      path     : '/msgBox',
      name     : 'msgBox',
      component: msgBox,
      meta     : {
        title: '',
      }
    },
    {
      path     : '/city',
      name     : 'cityList',
      component: cityList,
      meta     : {
        title: '',
      }
    },
    {
      path    : '*',
      redirect: '/'
    }
  ],
})

export default router



